export default {
	data() {
		return {
			active: -1,
			payList: [],
			vipTime: '',
			token: '',
			shopID: '',
		}
	},
	onLoad() {
		let self = this;
		uni.getStorage({
			key: 'user_info',
			success: function(res) {
				self.token = res.data.token
			}
		})
		this.getPayList();

	},
	methods: {
		onActive(idx, item) {
			this.shopID = item.id;
			this.active = idx;
			this.vipTime = item.duration * 60 * 60 * 1000;
			console.log(item.duration / 24)
			uni.showToast({
				title: item.title + ',价格:' + item.price + '元',
				icon: 'none',
			})
		},
		onPay() {
			if (!this.token) {
				uni.showToast({
					title: '暂未登录',
					icon: 'none',
				})
				return;
			}
			if (!this.shopID) {
				uni.showToast({
					title: '请选择充值类型',
					icon: 'none',
				})
				return;
			}
			var nowTime = Date.parse(new Date());
			// console.log(nowTime)
			nowTime = Number(this.vipTime + nowTime);
			// console.log(nowTime)
			var date = new Date(nowTime);
			var Y = date.getFullYear()
			var M = date.getMonth() + 1;
			var D = date.getDate();
			var h = date.getHours();
			var m = date.getMinutes();
			uni.showToast({
				title: Y + '-' + M + '-' + D + ' ' + h + ':' + m,
				icon: 'none',
			})


			uni.request({
				url: this.$serverUrl + '/recharge/create',
				data: {
					id: this.shopID
				},
				header: {
					// "Content-Type": "application/json;charset=UTF-8",
					"Content-Type": "application/x-www-form-urlencoded",
					"authorization": this.token
				},
				method: "POST",
				success: (ret) => {
					console.log(ret.data.data)
					if (ret.statusCode !== 200) {
						console.log('失败!');
					} else {
						// uni.requestPayment({
						// 	provider: 'alipay',
						// 	orderInfo: ret.data,
						// 	success: (e) => {
						// 		uni.showToast({
						// 			title: "支付成功"
						// 		})
						// 	},
						// 	fail: (e) => {
						// 		uni.showModal({
						// 			content: "抱歉，您的支付不成功",
						// 			showCancel: false
						// 		})
						// 	},
						// 	complete: () => {}
						// });
					}
				}
			});
		},
		getPayList() {
			uni.request({
				url: this.$serverUrl + '/recharge/list',
				data: {},
				header: {
					// "Content-Type": "application/json;charset=UTF-8",
					"Content-Type": "application/x-www-form-urlencoded",
					"authorization": this.token
				},
				method: "POST",
				success: (ret) => {
					if (ret.statusCode !== 200) {
						console.log('失败!');
					} else {
						console.log(ret.data.data)
						this.payList = ret.data.data
					}
				}
			});
		}
	}
}
